.CodeReviewMain {
  .containerCode {
    top: 109px;
    margin: 20px;
    border-radius: 4px;
    overflow: visible;
    padding: 14px 20px;
    width: 1086px;
    margin: 20px auto;
    font-family: "Poppins", sans-serif;

    .arrow-heading-wrapper {
      display: flex;
      align-items: center;
      padding-bottom: 20px;
      // border-bottom: 1px solid #ddd;
      margin: 20px 0 0 0px;
    }
    .arrow-div {
      background: #21745d1c;
      padding: 5px 10px;
      border-radius: 4px;
      display: flex;
      justify-content: center;
      margin-right: 10px;
      cursor: pointer;
    }
    .score-details-heading {
      font-size: 16px;
      font-weight: 700;
    }

    .table-container {
      margin: 5px 0px 20px;

      .resume-btn {
        color: #04724d;
        font-weight: 700;
        text-align: center;
        padding-left: 20px;
        font-family: "Poppins", sans-serif;
        font-size: 11px;
        display: flex;
        justify-content: center;
        cursor: pointer;
      }
      .resume-btn-disabled {
        color: #ccc;
        text-align: center;
        padding-left: 20px;
        font-weight: 700;
        font-family: "Poppins", sans-serif;
        font-size: 11px;
        display: flex;
        justify-content: center;
      }
    }

    .heading {
      display: flex;
      justify-content: space-between;
      .summary-heading {
        font-size: 16px;
        font-weight: 700;
      }
      .total-marks {
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 700;
      }
    }
    .details-container-header {
      margin: 20px 0;
      padding: 12px;
      background: #e9f3ef;
      border-radius: 5px;
      font-size: 14px;
      font-weight: 700;
      display: flex;
      justify-content: space-between;
      .marks-container {
        display: flex;
        justify-content: space-around;

        .total-marks-body {
          margin: 0 15px;
          text-transform: uppercase;
          font-size: 14px;
          font-weight: 600;
        }
      }
    }
    .details-container-body {
      background: #f1f3f6;
      margin: 20px 0;
      padding: 12px;
      border-radius: 5px;
      font-size: 14px;

      .tooltip-container {
        border-radius: 50%;
        margin-left: 3px;
        padding: 0px 3.5px;
        width: 20px;
        height: 20px;
        background: #c8a66e;
        .question-icon {
          color: #ffffff;
          font-size: small !important;
          margin-top: -3px;
        }
      }
      .coding-heading-container {
        font-weight: 700;
        display: flex;
        justify-content: flex-start;
        margin-top: 10px;
        padding: 0 20px;

        .coding-heading-question {
          width: calc(60% + 136px);
        }
        .coding-heading-rating {
          padding: 0 10px;
          width: 15%;
        }
        .coding-heading {
          padding: 0 10px;
          width: 120px;
          display: flex;
        }
      }
      .coding-body-container {
        margin-top: 20px;
        .CodingDisplay {
          margin: 12px;
          background-color: #e6ecf6;
          border-radius: 4px;
          padding: 10px;
          .questionArea {
            display: flex;
            // align-items: flex-start;
            width: 100%;
            .Question,
            .Screen,
            .Video,
            .Time,
            .Score {
              background-color: #fff;
              border-radius: 4px;
              margin-right: 8px;
              display: flex;
              align-items: center;
              justify-content: center;
            }
            .Question {
              width: 60%;
              padding: 10px 10px 0;
              max-height: 150px;
              overflow: auto;
              justify-content: flex-start;
              text-align: left;
              font-size: 11px;
              font-weight: 400;
              letter-spacing: 0px;
              color: #363636;
              opacity: 1;
            }
            .Screen {
              width: 60px;
              height: 40px;
            }
            .Video {
              width: 60px;
              height: 40px;
            }
            .Time {
              width: calc(30% - 136px);
              height: 40px;
              text-align: left;
              font-size: 12px;
              font-weight: 700;
              letter-spacing: 0px;
              color: #000000;
              opacity: 1;
            }
            .Score {
              width: 10%;
              margin-right: 0px;
              height: 40px;
              .ScoreCodeingDropDown {
                width: 100%;
                [class$="-control"] {
                  height: 100%;
                  border: none;
                }
                [class$="-indicatorSeparator"] {
                  background-color: #fff;
                }
                [class$="-ValueContainer"],
                [class$="-placeholder"] {
                  text-align: left;
                  font-size: 14px;
                  font-weight: 700;
                  letter-spacing: 0px;
                  justify-content: center;
                  color: #000000;
                  opacity: 1;
                }
              }
            }
          }
          .TableExecution {
            background-color: #fff;
            border-radius: 4px;
            margin: 10px 0;
            padding: 10px;
            width: 100%;
            .ExecuteTableHeader {
              background: #e6ecf6;
              border-radius: 5px;
              display: flex;
              padding: 10px 0;
              margin-bottom: 10px;
              div {
                text-align: center;
                width: 25%;
                font-size: 13px;
                font-weight: 700;
                letter-spacing: 0px;
                color: #363636;
                opacity: 1;
              }
            }
            .ExecuteBody {
              .bodyRow {
                display: flex;
                background: #ffffff;
                border: 0.4px solid #70707061;

                max-height: 100px;
                div {
                  width: 25%;
                  padding: 5px 10px;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  overflow: auto;
                  font-size: 12px;
                  font-weight: 500;
                  letter-spacing: 0px;
                  color: #363636;
                  opacity: 1;
                  border-right: 0.4px solid #70707061;
                }
                .View {
                  color: #04724d;
                  cursor: pointer;
                  border-right: none;
                  svg {
                    width: 17px;
                    margin-left: 2px;
                    margin-bottom: 2px;
                  }
                }
              }
              .bodyRow.active {
                background-color: #e9f3ef;
              }
            }
          }
        }
      }
    }
  }
  .share-btn1 {
    display: flex;
    justify-content: center;
  }
}
.resume-modal-Single {
  border-radius: 10px;
  outline: none;
  background-color: white;
  width: 500px;
  height: 700px;
  margin: auto;
  justify-content: center;
  align-items: center;
  font-family: Poppins;
  font-size: 16px;
  margin-top: 25px;
  bottom: 10px;
  .pg-viewer-wrapper,
  .pg-viewer-wrapper .document-container {
    width: calc(500px - 40px);
    height: calc(700px - 40px);
  }
}
.VideoPlayerCode {
  border-radius: 10px;
  outline: none;
  background-color: white;
  // height: 500px;
  // width: 700px;
  margin: auto;

  font-family: Poppins;
  font-size: 16px;
  position: absolute;
  top: calc(100% - 50% - 270px);
  left: calc(100% - 50% - 316px);
}
.ShareModalCode {
  .shareMainDiv {
    width: 60%;
    position: absolute;
    top: 20%;
    left: calc(100% - 50% - 30%);
    .titlee {
      color: #1e1e1e;
      opacity: 1;
      font-weight: bold;
      font-size: 15px;
    }
    .shareLink {
      background: #ffffff 0% 0% no-repeat padding-box;
      border-radius: 8px;
      opacity: 1;
      // padding: 15px 20px;
      font-family: "Poppins", sans-serif;

      .top {
        display: flex;
        justify-content: space-between;
        padding: 15px 20px 0;

        img {
          width: 24px;
          height: 24px;
        }
      }
      .line {
        border-bottom: #707070 1px solid;
        margin: 10px 0;
      }
      .linkArea {
        padding: 0 20px 15px;
        .LinkTitle {
          color: #000000;
          font-size: 17px;
          font-weight: 600;
        }
        .LinkSubTitle {
          color: #000000;
          font-size: 11px;
          font-weight: 500;
          padding: 10px 0;
        }
      }
    }
    .shareEmail {
      background: #ffffff 0% 0% no-repeat padding-box;
      border-radius: 8px;
      opacity: 1;
      padding: 15px 20px;
      margin-top: 10px;
      height: 155px;
      .multiselectEmail {
        margin-top: 5px;
        .skillsAuto .MuiInputBase-root {
          padding: 0 10px;
          display: flex;
          justify-content: center;
          align-items: center;
          height: auto !important;
          min-height: 34px !important;
          width: 100% !important;
          margin: 0 !important;
          border: 2px solid #ddd;
          border-radius: 4px;
          background: #fff;
        }

        .skillsAuto .MuiChip-outlined {
          border: 1px solid #333;
          color: #fff;
          background: black;
          height: 20px;
          margin: 5px;
        }
        .MuiChip-outlined .MuiChip-deleteIcon {
          color: #fff;
        }
      }
    }
  }
  .send-response-div {
    padding-top: 20px;
    float: right;

    .send-response-button {
      border-radius: 4px !important;
    }
  }
}
.resumeIconCoding {
  margin-right: 4px;
  cursor: pointer;
  background-color: #e6ecf6;
  border-radius: 50%;
  padding: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  svg {
    width: 15px;
  }
}

.score-tooltip-main {
  font-size: 9px;
  width: 150px;

  .main-wrapper-score {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
  }

  .score-heading {
    font-weight: bold;
    width: 33%;
  }

  .score-content {
    width: 33%;
    font-weight: normal;
  }
}
.score-tooltip-single {
  font-size: 9px;
  width: 150px;

  .main-wrapper-score {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
  }

  .score-heading {
    font-weight: bold;
    width: 33%;
  }

  .score-content {
    width: 33%;
    font-weight: normal;
  }
}
.resumeIconCode {
  margin-right: 4px;
  cursor: pointer;
  background-color: #e6ecf6;
  border-radius: 50%;
  padding: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  svg {
    width: 15px;
  }
}
.status-selectCode {
  margin-top: -5px;

  [class$="-control"],
  [class$="-containerr"] {
    min-height: 28px;
    height: 28px;
    width: 140px;
  }
  [class$="-placeholder"],
  [class$="-control "] {
    font-weight: 600;
  }
  [class$="-placeholder"] {
    font-size: 10px;

    // text-transform: lowercase;
  }
}
.CommentModalCode {
  .MuiCardContent-root {
    padding: 0;
  }
  border-radius: 4px;
  outline: none;
  background-color: white;
  width: 450px;
  height: 500px;
  bottom: 10px;
  right: 10px;
  position: absolute;
  .AreaComment {
    position: relative;
    .headerComment {
      background-color: #d5e5e3;
      padding: 15px 20px;
      display: flex;
      justify-content: space-between;
      text-align: left;
      font-weight: 700;
      font-size: 13px;
      letter-spacing: 0px;
      color: #21745d;
      opacity: 1;
      margin-bottom: 10px;
      img {
        cursor: pointer;
      }
    }
    .NewCommentSection {
      height: 150px;
      bottom: -170px;
      width: calc(100% - 20px);
      margin: 0 10px 10px 10px;
      background-color: #f1f3f6;
      padding: 10px;
      position: absolute;
      border-radius: 10px;
      .commentTextArea {
        border: 1px solid #d8d8d8;
        border-radius: 6px;
        width: 100%;
        outline: none;
      }
      .sendComment {
        display: flex;
        justify-content: flex-end;
        margin-top: 10px;
      }
    }
    .OldCommentSection {
      height: calc(450px - 130px - 55px);
      overflow: auto;
      font-family: "Poppins", sans-serif;
      .oldcommentText {
        background: #ffffff;
        border: 1px solid #d8d8d8;
        border-radius: 5px;
        opacity: 1;
        margin: 5px 10px;
        padding: 15px 20px;
        .userName {
          text-align: left;
          font-weight: 700;
          font-size: 11px;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
        .timeComment {
          text-align: left;
          font-weight: 400;
          font-size: 9px;
          letter-spacing: 0px;
          color: #363636;
          opacity: 1;
          margin: 2px 0 10px 0;
        }
        .noteComment {
          text-align: left;
          font-weight: 400;
          font-size: 11px;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
      }
    }
  }
}
.Linkcopy {
  display: flex;
  justify-content: space-between;
  font-family: "Poppins", sans-serif;
  .linkcopy-input {
    .MuiInputBase-root.Mui-disabled {
      height: 34px;
    }
    .MuiInputBase-root {
      border: none;
    }
  }
  .copy-button {
    background: #fff !important;
    color: #04724d !important;
    border: 1px solid #04724d !important;
    border-radius: 4px !important;
    margin: 0 10px;
    width: 150px !important;
  }
}
.CodeEditor {
  width: 80%;
  height: 80%;
  display: flex;
  background-color: #f1f3f6;
  padding: 20px;
  justify-content: space-between;
  position: absolute;
  top: 10%;
  left: 10%;
  .CodePart {
    width: calc(60% - 10px);
    height: calc(100% - 40px);
    background-color: #fff;
    border-radius: 8px;
    .codeEditor {
      width: 100% !important;
      height: calc(100% - 10px) !important;
      border-radius: 0 0 8px 8px;
    }
    .Language {
      width: 100%;
      border-bottom: 1px solid #c1c1c1;
      display: flex;
      align-items: center;
      padding: 8px;
      .LanguageingDropDown {
        width: 200px;
      }
    }
  }
  .SolutionPart {
    width: calc(40% - 10px);
    background-color: #fff;
    border-radius: 8px;
    .TabSwitch {
      width: 100%;
      display: flex;
      border-bottom: 1px solid #c1c1c1;
      .Tab {
        width: 50%;
        height: 51px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 12px;
        font-weight: 600;
        cursor: pointer;
      }
    }
    .DisplayArea {
      padding: 20px;
      height: calc(100% - 60px);
      overflow: auto;
      text-align: left;
      font-size: 13px;
      font-weight: 600;
      letter-spacing: 0px;
      // color: #000000;
      opacity: 1;
      pre {
        word-wrap: break-word;
        white-space: break-spaces;
      }
    }
  }
}
